footer {
  background-color: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}
footer a {
  color: var(--color-bg);
}
.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
  transition: var(--transition);
}
.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
  transition: var(--transition);
}
.permalinks li a {
  transition: all 0.3s linear;
  display: inline-block;
}

.permalinks li a:hover {
  box-shadow: 0 0.7rem 0.9rem rgba(0, 0, 0, 0.8);
  color: #000;
}

.footer__icons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}
.footer__icons a {
  font-size: 1.4rem;
  transition: var(--transition);
  background-color: var(--color-bg);
  color: var(--color-white);
  padding: .8rem;
  border-radius: .7rem;
  display: flex;
  border: 1px solid transparent;
}
.footer__icons a:hover{
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}
.footer__copyright{
  margin-bottom: 4rem;
  color: var(--color-bg);

}

/* ================== media queries ============ */
@media screen and (max-width:600px){
  .permalinks{
    flex-direction: column;
    gap: 1rem;
  }
}
